<template>
	<div>
		<template v-if="loadingUsers">
			<div class="div__loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Search result for {{ query }}</h3>
				</div>

				<div class="d-flex">
					<div class="dropdown">
						<div class="btn__filter dropdown-toggle" id="dropdownButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
							<span class="btn__filter__icon">
								<svg width="24" height="24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M13 18h-2c-.55 0-1-.45-1-1s.45-1 1-1h2c.55 0 1 .45 1 1s-.45 1-1 1zm8-11c0 .55-.45 1-1 1H4c-.55 0-1-.45-1-1s.45-1 1-1h16c.55 0 1 .45 1 1zm-4 6H7c-.55 0-1-.45-1-1s.45-1 1-1h10c.55 0 1 .45 1 1s-.45 1-1 1z" fill="#333"/></svg>
							</span>
							Filter
						</div>
					</div>
					<button class="btn btn__primary" type="button" name="button"
					data-toggle="modal" data-target="#customerModal">
					<span class="btn__icon">
						<svg width="24" height="13" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z" fill="#fff"/></svg>
					</span>Add User</button>
				</div>
			</div>
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts--list__wrapper">
					<div class="contacts--list__item head">
						<div></div>
						<p>Basic info</p>
						<p>Date of Birth</p>
						<p>phone</p>
						<p>Address</p>
						<p>Actions</p>
					</div>
					<div class="contacts__body">
						<template v-if="usersLoaded && users.length > 0">
							<SearchItem
								v-for="userData in users"
								:key="userData.id"
								:user-data="userData"
							>
								&nbsp;
							</SearchItem>
						</template>
						<template v-else-if="usersLoaded">
							<p>No user found</p>
						</template>
						<template v-else>
							<p>An error occurred while loading users, please try again.</p>
						</template>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import { searchUser } from '../../utils/api/users';
import Loader from '../../components/Loader';
import SearchItem from './SearchItem';

export default {
	name: 'Search',
	data() {
		return {
			users: [],
			loadingUsers: true,
			usersLoaded: false,
			loading: true,
			query: ''
		};
	},
	watch: {
		$route() {
			this.searchUser();
		}
	},
	created() {
		this.searchUser();
	},
	methods: {
		searchUser() {
			this.query = this.$route.params.query;
			searchUser(this.query).then((response) => {
				if (response.status === 200) {
					this.users = response.data.data;
					this.usersLoaded = true;
					return true;
				}
				this.usersLoaded = false;
				return false;
			}).catch((error) => {
				this.usersLoaded = false;
			}).finally(() => {
				this.loadingUsers = false;
			});
		}
	},
	components: {
		SearchItem,
		Loader
	}
};
</script>

<style lang="scss" scoped>
.main {
	h3 {
		padding-bottom: 0;
	}
}
</style>
